import React, { useEffect } from 'react'

import Papa from 'papaparse'

import sourceFile from './IELTSVocab.csv'

import styled from 'styled-components'

import FlashCard from './FlashCard'

const BackGround = styled.div`

    background-image: url('https://i.pinimg.com/736x/c1/2f/31/c12f31ab8121d736f4cac5c66c36f5b4.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

`

const FlashCardData = () => {

    useEffect(() => {

        async function getData() {
            const response = await fetch(sourceFile);
            const reader = response.body.getReader();
            const result = await reader.read(); // raw array
            const decoder = new TextDecoder("utf-8");
            const csv = decoder.decode(result.value); // the csv text
            const results = Papa.parse(csv, { header: true }); // object with { data, errors, meta }
            const rows = results.data; // array of objects
            localStorage.setItem('words', JSON.stringify(rows));
        }
        getData()

    }, [])

    return (

        <>
            <BackGround>
                <FlashCard />
            </BackGround>
            


        </>


    )

}

export default FlashCardData