import React from 'react'

import { BrowserRouter, Routes, Route } from 'react-router-dom'

import HomePage from './component/HomePage';
import FlashCardData from './component/FlashCardData';

function App() {
  return (
    <BrowserRouter>
      
        <Routes>

          <Route

              path = '/'
              exact
              element = {<HomePage />}

          ></Route>

          <Route

              path = '/flashcard'
              exact
              element = {<FlashCardData />}

          />

        </Routes>

    </BrowserRouter>
  );
}

export default App;
